header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ============= CTA ================== */

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* =============Header Socials ================ */

.header__socials {
  display: flex;
  flex-direction: column;
  align-content: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials > a {
  font-size: 3rem;
}

.header__socials::after {
  content: "";
  display: block;
  width: 5px;
  height: 3rem;
  background: var(--color-primary);
  margin-top: 1rem;
}

/* =============ME ================ */

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  position: absolute;
  height: 30rem;
  left: calc(50% - 11rem);
  margin-top: 2rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.me__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* ============= Scroll down ================ */

.scroll__down {
  position: absolute;
  bottom: 5rem;
  right: -2.3rem;
  transform: rotate(90deg);
  font-size: 1rem;
  font-weight: 300;
  color: var(--color-primary);
  /*animation: scroll-down 1.5s infinite;*/
}

/* media queries (medium devices) */

@media screen and (max-width: 1024px) {
  .header {
    height: 68vh;
  }
}

/* media queries (small devices) */
@media screen and (max-width: 600px) {
  .header {
    height: 100vh;
  }

  .header__socials,
  .scroll__down {
    display: none;
  }
}
